strong {
  // font-weight: 400
 }  // +bold

small {
  font-size: inherit; }

em, i {
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  font-size: 36px;
  font-weight: 700;
  strong {
    @include clar;
    font-weight: 400;
    sup {
      vertical-align: super;
      font-size: .5em; } }

  &--purple {
    color: $purple; }
  &--accent {
    color: $accent; }
  &--ttu {
    text-transform: uppercase; } }

.h2, h2 {
  font-size: 32px;
  font-weight: 700;

  &--accent {
    color: $accent; } }

.h3, h3 {}

.h4, h4 {}

.h5, h5 {}

.h6, h6 {}

.title-under {
  position: relative;
  padding-bottom: 20px;
  @include before {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    height: 7px;
    width: 30px;
    background-color: $accent;
    border-radius: 7px; } }

.title-bg {
  color: $white;
  display: inline-block;
  position: relative;
  span {
    position: relative;
    z-index: 1; }
  img {
    @include vcenter;
    width: 105%;
    max-width: 105%;
    z-index: 0;
    pointer-events: none; } }
