.item-links {
  @include flex(flex-start, normal, row, wrap);
  max-width: 1645px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  padding-bottom: 50px; }

.item-link {
  $this: &;
  @include flex(flex-start, normal, column);
  margin-right: 15px;
  margin-left: 15px;
  width: calc(#{percentage(1/7)} - 30px);
  @include hover {
    #{$this}__img {
      border-color: $blue; } }
  &__img {}

  &__title {
    color: $blue;
    font-weight: 700;
    display: block;
    // position: absolute
    // bottom: 15px
    // left: 50%
 } }    // transform: translate(-50%, 0)

.item-link-img {
  background-color: $white;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
  border-radius: $radius;
  margin-bottom: 20px;
  background-size: contain;
  flex-grow: 1;
  padding-top: 25px;
  padding-bottom: 15px;
  position: relative;
  overflow: hidden;
  @include flex(flex-end, center, column);
  border: 2px solid  $white;
  @include tr(border-color .3s);
  @include hover {
    border-color: $blue; }
  img {
    transform: translate(-20%, 0);
    display: block;
    margin-bottom: 20px; } }
