$icons: (0:0);
$icons: map-merge($icons,('arrow-left-white': (X: 0px, Y:0px, W: 266px, H: 122px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-left': (X: 0px, Y:-264px, W: 154px, H: 70px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-right-white': (X: 0px, Y:-132px, W: 266px, H: 122px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('comet': (X: -276px, Y:0px, W: 124px, H: 142px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star': (X: -276px, Y:-152px, W: 62px, H: 48px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star2-purple': (X: -329px, Y:-210px, W: 39px, H: 40px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star2': (X: -164px, Y:-264px, W: 39px, H: 40px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star4-purple': (X: -348px, Y:-152px, W: 43px, H: 40px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star4': (X: -276px, Y:-210px, W: 43px, H: 40px, TW: 400px, TH: 334px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

