.slider {
  $this: &;
  &__wrap {
    position: relative; }
  &__prev,
  &__next {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 1;
    display: inline-block;
    height: 45px;
    width: 45px;
    background-color: $yellow;
    color: $white;
    border-radius: 50%;
    @include tr(color .3s);
    @include before {
      @include icon-arrow(14, 14, 2, currentColor, 45);
      display: inline-block;
      @include vcenter;
      transform: translate(-50%, -50%) rotate(45deg); } }
  &__prev {
    left: 0;
    @include before {
      left: 25px; } }
  &__next {
    right: 0;
    transform: translate(0, -50%) rotate(180deg);
    @include before {
      left: 25px; } }


  &--center {
    &__wrap {
      .swiper-container {
        padding-top: 30px;
        padding-bottom: 30px;
        max-width: 1230px;
        margin-left: auto;
        margin-right: auto;
        // padding-left: 90px
 } }        // padding-right: 90px
    .slide {
      @include tr(transform .3s);
      &.swiper-slide-active {
        @include scale(1.2);
        z-index: 1;
        .slide__img {
          @include before {
            visibility: hidden;
            opacity: 0; } } }
      &__img {
        border-radius: $radius;
        @include before {
          @include coverdiv;
          background-color: rgba($purple, .5);
          border-radius: $radius;
          @include tr(opacity .3s, visibility .3s); } } } } }


.slide {
  &__img {
    padding-top: 60%;
    @include cover;
    position: relative; } }
